
.page-task-send {
  padding-right: 20px;
}
.page-task-send .theme-data {
  position: absolute;
  top: 148px;
  right: 37%;
  z-index: 1000;
}
.page-task-send .el-steps {
  text-align: center;
}
.page-task-send .el-steps .el-step:first-child .el-step__icon {
  cursor: pointer;
}
.page-task-send .el-row {
  margin: 50px auto;
}
.page-task-send .el-select {
  width: 100%;
}
.page-task-send .layout-right {
  text-align: center;
}
.page-task-send .layout-right .el-form {
  text-align: left;
}
.page-task-send .layout-right .right-content {
  padding: 50px;
  background-color: #fff;
}
.page-task-send .btn-try {
  width: 200px;
  height: 44px;
  border-radius: 26px;
}
.page-task-send .btn-send {
  width: 250px;
  height: 50px;
  border-radius: 28px;
  margin-top: 60px;
}


