
.page-tpl-web {
  background-color: #fff;
  width: 640px;
}
.page-tpl-web span {
  color: #ff483a;
}
.page-tpl-web .view-main {
  padding: 0 60px 23px;
  width: 520px;
}
.page-tpl-web .banner {
  background: url("../assets/images/banner.png") no-repeat center;
  background-size: 100% 210px;
  width: 640px;
  height: 210px;
}
.page-tpl-web .subject {
  margin: 40px auto 20px;
}
.page-tpl-web .subject .hello {
  font-size: 20px;
  padding-bottom: 20px;
  color: #333;
}
.page-tpl-web .subject .where {
  font-size: 14px;
  color: #151515;
}
.page-tpl-web .company {
  min-width: 450px;
  box-shadow: 0 1px 10px 1px rgba(0,0,0,0.2);
  color: #151515;
}
.page-tpl-web .company .header {
  position: relative;
  padding: 30px 35px 25px;
  border-bottom: 1px solid #ddd;
}
.page-tpl-web .company .company-name {
  font-size: 18px;
  padding-bottom: 5px;
}
.page-tpl-web .company .content {
  padding: 0 30px 30px;
}
.page-tpl-web .company .content-item {
  padding-top: 30px;
}
.page-tpl-web .company .tips {
  padding-top: 20px;
  text-align: right;
  font-size: 12px;
}
.page-tpl-web .company h3 {
  font-size: 16px;
}
.page-tpl-web .company ul {
  color: #666;
}
.page-tpl-web .btn {
  position: absolute;
  top: 30px;
  right: 30px;
  display: inline-block;
  width: 108px;
  height: 30px;
  line-height: 30px;
  background-color: #ff483a;
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 200;
  text-align: center;
  cursor: pointer;
}
.page-tpl-web .overview-company,
.page-tpl-web .overview-employee,
.page-tpl-web .overview-opportunity {
  text-align: center;
  margin-top: 40px;
}
.page-tpl-web .overview-company .title,
.page-tpl-web .overview-employee .title,
.page-tpl-web .overview-opportunity .title {
  position: relative;
  font-size: 24px;
}
.page-tpl-web .overview-company .title .divider-left,
.page-tpl-web .overview-employee .title .divider-left,
.page-tpl-web .overview-opportunity .title .divider-left,
.page-tpl-web .overview-company .title .divider-right,
.page-tpl-web .overview-employee .title .divider-right,
.page-tpl-web .overview-opportunity .title .divider-right {
  position: absolute;
  width: 25%;
  height: 1px;
  top: 50%;
  margin-right: -0.5px;
  background-color: #bcd0d7;
}
.page-tpl-web .overview-company .title .divider-left,
.page-tpl-web .overview-employee .title .divider-left,
.page-tpl-web .overview-opportunity .title .divider-left {
  left: 0;
}
.page-tpl-web .overview-company .title .divider-right,
.page-tpl-web .overview-employee .title .divider-right,
.page-tpl-web .overview-opportunity .title .divider-right {
  right: 0;
}
.page-tpl-web .overview-company .list,
.page-tpl-web .overview-employee .list,
.page-tpl-web .overview-opportunity .list {
  padding-top: 38px;
  font-size: 0;
}
.page-tpl-web .overview-company .list li,
.page-tpl-web .overview-employee .list li,
.page-tpl-web .overview-opportunity .list li {
  display: inline-block;
  font-size: 14px;
  width: 30%;
}
.page-tpl-web .overview-company .list li:nth-child(2),
.page-tpl-web .overview-employee .list li:nth-child(2),
.page-tpl-web .overview-opportunity .list li:nth-child(2) {
  padding: 0 25px;
}
.page-tpl-web .overview-company .desc-title,
.page-tpl-web .overview-employee .desc-title,
.page-tpl-web .overview-opportunity .desc-title {
  padding: 22px 0 12px;
  font-size: 20px;
}
.page-tpl-web .overview-company .warning,
.page-tpl-web .overview-employee .warning,
.page-tpl-web .overview-opportunity .warning {
  padding: 30px 0 40px;
  text-align: left;
}
.page-tpl-web .info-from {
  font-size: 12px;
  text-align: center;
  padding-top: 40px;
}
.page-tpl-web .note,
.page-tpl-web .user-action {
  color: #999;
  font-size: 12px;
}
.page-tpl-web .note {
  padding: 40px 0 30px;
  text-align: center;
}
.page-tpl-web .note a {
  color: #20a0ff;
  padding-left: 5px;
  cursor: pointer;
}
.page-tpl-web .user-action {
  text-align: right;
}


