html,body{
  height: 100%;
  width: 100%;
  overflow: hidden;
}
body{
  color: #313131;
  background-color: #f5f5f5;
}

a {
  color: #313131;
  text-decoration: none;
  transition: color 0.2s linear;
}
a:hover {
  color: #ff8331;
}

body, button, input, textarea, select {
  font: 14px/1.5 Helvetica, Arial, "PingFang SC", "Helvetica Neue", "Lantinghei SC","Hiragino Sans GB", STHeiti, "Microsoft Yahei","Microsoft Sans Serif", sans-serif, \5b8b\4f53;
}

select {
  -webkit-appearance: none;
}

* {
  outline: none;
}

span {
  font-weight: inherit;
}

img, canvas {
  vertical-align: middle;
}

label {
  cursor: text;
}

.placeholder {
  color: #ccc;
}

::-webkit-input-placeholder {
  color: #ccc;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: #ccc;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: #ccc;
}

:-ms-input-placeholder {
  color: #ccc;
}
.edm-page{
  width: 100%;
  height: 100%;
  overflow: hidden;
}


