
.page-edm {
  position: relative;
}
.page-edm .btn-container {
  position: fixed;
  right: 50px;
  top: 20px;
}
.page-edm .input-item-disabled .el-input__inner,
.page-edm .input-item-disabled .el-textarea__inner {
  border: none;
  background-color: #f5f5f5;
  resize: none;
}
.page-edm .item-group {
  position: relative;
  width: 600px;
}
.page-edm .item-group .el-form-item {
  width: 400px;
}
.page-edm .item-group .el-button {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}
.page-edm .item-group:hover .el-button {
  display: inline-block;
}


